import React from 'react'
import '../../styles/musculos.css'
import { useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import { NavLink } from 'react-router-dom';

export default function Trapecios() {
    useEffect(() => {
        window.scrollTo(0, 0); // Desplaza la ventana al principio cuando la ubicacin cambie
      }, []);
       /* selector */
       /* const [selectedSex, setSelectedSex] = useState("male");

       function handleSex(option) {
         setSelectedSex(option)
       } */
  return (
    <>
    <div className='container-menu'>
        {/* <div className='btn-group-sex'>
            <div className='input-container'>
                <input className="js-sex-option" type="radio" name="sexchooser" id="sexchoosermale" value="male" onClick={() =>handleSex("male")} defaultChecked/> 
                <label htmlFor='sexchoosermale'>Hombre</label>
            </div>

            <div className='input-container'>
            <input className="js-sex-option" type="radio" name="sexchooser" id="sexchooserfemale" value="female" onClick={() =>handleSex("female")} /> 
                    <label htmlFor='sexchooserfemale'>Mujer</label>
            </div>
        </div> */}
        <div className='main-nav'>
            <div className='section-menu'>
            <NavLink className='links' to='/ejercicios/pecho'><button className='section-button'>Pecho</button></NavLink>
                <NavLink className='links' to='/ejercicios/hombro'><button className='section-button'>Hombro</button></NavLink>
                <NavLink className='links' to='/ejercicios/bicep'><button className='section-button'>Bicep</button></NavLink>
                <NavLink className='links' to='/ejercicios/tricep'><button className='section-button'>Tricep</button></NavLink>
                <NavLink className='links' to='/ejercicios/antebrazo'><button className='section-button'>Antebrazo</button></NavLink>
                <NavLink className='links' to='/ejercicios/trapecios'><button className='section-button'>Trapecios</button></NavLink>
                <NavLink className='links' to='/ejercicios/espalda-media'><button className='section-button'>Espalda media</button></NavLink>
                <NavLink className='links' to='/ejercicios/dorsales'><button className='section-button'>Dorsales</button></NavLink>
                <NavLink className='links' to='/ejercicios/espalda-baja'><button className='section-button'>Espalda Baja</button></NavLink>
                <NavLink className='links' to='/ejercicios/abdomen'><button className='section-button'>Abdomen</button></NavLink>
                <NavLink className='links' to='/ejercicios/cudriceps'><button className='section-button'>Cudriceps</button></NavLink>
                <NavLink className='links' to='/ejercicios/isquiotibiales'><button className='section-button'>Isquiotibiales</button></NavLink>
                <NavLink className='links' to='/ejercicios/gemelos'><button className='section-button'>Gemelos</button></NavLink>
                <NavLink className='links' to='/ejercicios/glteos'><button className='section-button'>Glteos</button></NavLink>
            </div>
        </div>

        {/* CONTENEDORES DE CADA MSCULO CON FUNCIN DE SELECTOR */}
    </div>
    
    <div className='contenedor-ejercicios'>
        <div className='muscle-title'>
        <h1>Trapecios</h1>
        </div>

        <div className='ejercicio'>
        <h2>Encogimiento de hombros sentado con mancuernas</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Principiante</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-dumbbell-seated-shrug-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-dumbbell-seated-shrug-side.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Sintese en un banco con mancuernas en ambas manos, con las palmas hacia el cuerpo y la espalda recta.</li>
            <li>Eleve los hombros y mantenga la posicin contrada en el vrtice del movimiento.</li>
            <li>Baje lentamente los hombros a la posicin inicial.</li>
  
        </ol>
        </div>

        <div className='ejercicio'>
        <h2>Encogimiento de hombros de espalda con barra</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Intermedia</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-barbell-silverback-shrug-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-barbell-silverback-shrug-side.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Prese con los pies separados al ancho de los hombros sosteniendo la barra con ambas manos al frente, justo pasando el ancho de los hombros.</li>
            <li>Inclnese hacia adelante a la altura de las caderas con una ligera flexin de las rodillas, manteniendo la espalda recta.</li>
            <li>Engancha tus omplatos, como si estuvieras tratando de tocarlos y luego suelte el encogimiento de hombros.</li>
  
        </ol>
        </div>

        <div className='ejercicio'>
        <h2>Encogimiento de hombros con mancuernas</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Principiante</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-dumbbell-shrug-front_8g0TOxX.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-dumbbell-shrug-side_vS2pKIC.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Ponte de pie con dos mancuernas. Levanta los omplatos. Dar un apretn de un segundo en la parte superior.</li>
  
        </ol>
        </div>

        <div className='ejercicio'>
        <h2>Remo vertical con barra</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Avanzada</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-barbell-upright-row-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-barbell-upright-row-side.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Tome un agarre doble por encima de la cabeza aproximadamente al ancho de los hombros.</li>
            <li>Tire de los codos hacia arriba del techo. Trate de llevar la barra al nivel de la barbilla o un poco ms arriba.</li>
  
        </ol>
        </div>

        <div className='ejercicio'>
        <h2>Encogimientos de hombros en mquina Smith</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Principiante</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-standing-smith-machine-shrugs-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-standing-smith-machine-shrugs-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Coloque la barra ms abajo para que cuando la sujete sus brazos estn completamente extendidos y su espalda est recta.</li>
            <li>Agarre al ancho de los hombros, levante la barra con los hombros y haga una pausa en la posicin contrada.</li>
            <li>Baje lentamente la barra a la posicin inicial.</li>
  
        </ol>
        </div>

        <div className='ejercicio'>
        <h2>Encogimiento de hombros inclinado con pesas rusas</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Intermedia</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-kettlebell-incline-shrug-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-kettlebell-incline-shrug-side.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Apoyado en la parte trasera del banco, con los pies bien apoyados en el suelo y los brazos colgando a los lados.</li>
            <li>Sosteniendo la pesa rusa con ambas manos, enganche los omplatos, como si estuviera tratando de tocarlos. Suelta el encogimiento de hombros.</li>
        </ol>
        </div>

        <div className='ejercicio'>
        <h2>Encogimiento de hombros en polea</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Intermedia</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-cable-shrug-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-cable-shrug-side.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Ubique ambas poleas hasta abajo del todo y coloque un agarre de manija.</li>
            <li>Tome ambas manijas y asegrese de estar centrado en la mquina. Deje que sus brazos cuelguen libremente.</li>
            <li>Tire de los omplatos hacia arriba y hacia las orejas. Deje que sus omplatos vuelvan a la posicin inicial.</li>
  
        </ol>
        </div>

        <div className='ejercicio'>
        <h2>Remo vertical con cable</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Avanzada</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-cable-upright-row-side.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-cable-upright-row-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Use un accesorio de barra con el cable colocado en la parte inferior de la mquina.</li>
            <li>Tire de los codos hacia el techo hasta que el accesorio quede justo debajo de la barbilla y luego extindalos hasta que los codos estn completamente extendidos.</li>
  
        </ol>
        </div>

        <div className='ejercicio'>
        <h2>Encogimiento de hombros con barra</h2>
        <hr className='bar-separate'/>
        <h3>Dificultad: Principiante</h3>
        <div className='image-exercise'>
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-barbell-shrug-front.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
            <ReactPlayer
            url={'https://media.musclewiki.com/media/uploads/videos/branded/male-barbell-shrug-side.mp4#t=0.1'}
            width='100%'
            height='100%'
            volume='0.1'
            muted
            playing= 'true'
            controls
            />
        </div>
        <ol className='instrucciones-ejercicio'>
            <li>Comience parndose con los pies separados al ancho de las caderas, con una barra frente a usted.</li>
            <li>Agchese y tome la barra con un agarre por encima de la cabeza, con las manos un poco ms separadas que el ancho de los hombros. Mantn la espalda recta y los hombros hacia abajo mientras sujetas la barra.</li>
            <li>Prate derecho, levanta la barra del suelo y tira de ella hacia tus caderas. Mantenga los codos cerca de su cuerpo y los hombros hacia abajo mientras hace esto.</li>
            <li>Una vez que la barra est en tus caderas, levanta los hombros lo ms alto que puedas, manteniendo la posicin por un momento.</li>
        </ol>
        </div>
    </div>
    </>
  )
}