import React from 'react';
import '../styles/calentamientos.css';
import { useEffect } from 'react';
import ReactPlayer from 'react-player';

export default function Calentamientos() {
    useEffect(() => {
        window.scrollTo(0, 0); // Desplaza la ventana al principio cuando la ubicacin cambie
      }, []);
  return (
    <div id='primary'>
    <article>
        <header className='header-article'>
             <h1 className='h1-header'>Calentamientos</h1>
            <p>El calentamiento antes de iniciar un entrenamiento es de suma importancia debido a sus numerosos beneficios para el cuerpo. Esta fase inicial de actividad fsica suave y progresiva tiene el propsito de preparar al organismo de manera adecuada para el ejercicio que est por venir. Durante el calentamiento, el flujo sanguneo aumenta, lo que mejora la oxigenacin de los msculos y tejidos, preparndolos para un rendimiento ptimo. Adems, el calentamiento incrementa la temperatura corporal, lo que facilita la elasticidad y flexibilidad muscular, reduciendo as el riesgo de lesiones. <b>Debemos calentar para no lesionarnos!</b></p>
        </header>
        <div className='info-container'>
             <h1 className='h1-article'>1- Calentamiento para espalda:</h1>
             <p>Este podra ser un buen calentamiento antes de una sesin de entrenamiento de espalda.</p>
             <div>
             <ReactPlayer
             url={require('../videos/CalentamientoEspalda.mp4')}
             width='100%'
             height='80%'
             controls
             volume='0.1'
             muted
             playing= 'true'
             />
             </div>

             <h1 className='h1-article'>2- Calentamiento para hombros y pecho:</h1>
             <p>Calentamiento que podra realizarse tanto para hombros (principalmente) y para antes de arrancar a hacer pecho debido a que como sabemos, a la hora de hacer un press banca o cualquier otro press involucramos los hombros y lo ideal sera que los hagamos entrar en calor.</p>
             <div>
             <ReactPlayer
             url={require('../videos/CalentamientoHombros.mp4')}
             width='100%'
             height='80%'
             controls
             volume='0.1'
             muted
             playing= 'true'
             />
             </div>

             <h1 className='h1-article'>3- Calentamiento y movilidad articular para piernas:</h1>
             <p>El calentamiento de piernas es uno de los ms importantes ya que por lo general lo hacemos mal o muy rpido, es importante tomarnos nuestro tiempo en realizarlo y sobre todo cuando vamos a hacer un ejercicio que involucre varios msculos como por ejemplo la sentadilla la cual es esencial activar el core como se observa en el segundo video.</p>
             <div>
             <ReactPlayer
             url={require('../videos/CalentamientoPiernas.mp4')}
             width='100%'
             height='80%'
             controls
             volume='0.1'
             muted
             playing= 'true'
             />
             </div>
             <div>
             <ReactPlayer
             url={require('../videos/CalentamientoLumbar.mp4')}
             width='100%'
             height='80%'
             controls
             volume='0.1'
             muted
             playing= 'true'
             />
             </div>
             <p>Y en el caso de tener problemas en las rodillas una buena movilidad articular podra mejorar tu sesin de entrenamiento.</p>
             <div>
             <ReactPlayer
             url={require('../videos/CalentamientoRodillas.mp4')}
             width='100%'
             height='80%'
             controls
             volume='0.1'
             muted
             playing= 'true'
             />
             </div>
        </div>
    </article>

</div>
  )
}
