import React from 'react';
import '../styles/porque-no-noto-cambios.css';
import { useEffect } from 'react';

export default function SubirOBajarDePeso() {
  useEffect(() => {
    window.scrollTo(0, 0); // Desplaza la ventana al principio cuando la ubicacin cambie
  }, []);
  return (
    <div id='primary'>
    <article>
        <header className='header-article'>
             <h1 className='h1-header'>Subir o Bajar de peso</h1>
            <p className='p-header'>A continuacin veremos algunos factores por los cuales no logras subir o bajar de peso</p>
        </header>
        <div className='info-container'>
             <h1 className='h1-article'>Desequilibrio calrico</h1>
             <p>Para subir o bajar de peso, es necesario tener un equilibrio calrico adecuado. Si ests tratando de subir de peso y no lo logras, es posible que no ests consumiendo suficientes caloras para crear un excedente energtico. Por otro lado, si ests tratando de bajar de peso y no ves resultados, puede ser que no ests creando un dficit calrico adecuado. Es importante asegurarte de que tu ingesta de caloras est ajustada segn tus objetivos especficos.</p>
             <h1 className='h1-article'>Seleccin de alimentos</h1>
             <p>La calidad de los alimentos que consumes tambin es importante. Si ests tratando de subir de peso, es recomendable enfocarte en alimentos ricos en nutrientes y caloras, como protenas magras, carbohidratos complejos y grasas saludables. Por otro lado, si ests tratando de bajar de peso, es importante elegir alimentos nutritivos y bajos en caloras, como frutas, verduras, protenas magras y granos integrales.</p>
            <h1 className='h1-article'>Factores hormonales o metablicos</h1>
            <p>Algunas personas pueden tener desequilibrios hormonales o metablicos que dificultan el proceso de subir o bajar de peso. Si sospechas que esto podra ser un problema, es recomendable consultar a un mdico o endocrinlogo para una evaluacin ms detallada.</p>
            <h1 className='h1-article'>Niveles de actividad fsica</h1>
            <p>Tu nivel de actividad fsica puede influir en tu capacidad para subir o bajar de peso. Si ests tratando de subir de peso y no ves resultados, puede ser que tu actividad fsica est quemando demasiadas caloras. Por otro lado, si ests tratando de bajar de peso y no ves resultados, es posible que necesites aumentar la cantidad o la intensidad de tu actividad fsica para crear un mayor dficit calrico.</p>
            <h1 className='h1-article'>Factores genticos</h1>
            <p>La gentica tambin puede desempear un papel en tu capacidad para subir o bajar de peso. Algunas personas tienen una mayor predisposicin gentica a tener dificultades para modificar su peso corporal. Aunque los factores genticos no pueden cambiarse, an es posible lograr cambios saludables en el peso a travs de la alimentacin y el ejercicio adecuados.</p>
           
           <p><b>Recuerda que cada persona es nica y los resultados pueden variar. Si ests teniendo dificultades para subir o bajar de peso, considera buscar la ayuda de un profesional de la salud, como un dietista nutricionista o un entrenador personal, quienes pueden proporcionarte un enfoque personalizado y brindarte el apoyo necesario para alcanzar tus objetivos.</b></p>
        </div>
    </article>

</div>
  )
}
